<template>
  <div class="matter">
    <div>
      <van-nav-bar left-text="返回" :title="xsurlN" left-arrow @click-left="back" />
    </div>
    <div class="matter-box" ref="matterBox">
      <P class="name">{{this.xsurlN}}</P>
      <div class="text" v-for="(item,index) in matterData.list" :key="index">
        <p>{{item}}</p>
      </div>
      <div class="btn clearfix">
        <div class="fl" @click="goto">上一章</div>
        <div class="fl" @click="getdetails()">返回目录</div>
        <div class="fl" @click="getto">下一章</div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("matterModule");
export default {
  name: "metter",

  data() {
    return {
      xsurlL: [],
      xsurlN: [],
      conUrl: [],
      xsUrl: [],
      i: 0
    };
  },

  computed: {
    ...mapState(["matterData"])
  },
  methods: {
    ...mapMutations(["getmatterData", "resetState"]),

    back() {
      this.$router.go(-1);
    },

    getdetails() {
    
      this.$router.push({
        name: "details",
        query: {
          url: this.xsUrl
        }
      }); 
      //  console.log("this.xsUrl ==>> ", this.xsUrl )
    },

    gotoSection() {
      let self = this;

      let i = this.i;

      let URL = this.$route.query.xsUrl;
      // console.log("URL ==>> ", URL);

      if (this.$route.query.i != undefined) {
        // console.log("this.i ==>> ", this.i);

        // 小说id
        let xsUrl = this.$route.query.xsUrl;
        // console.log("xsUrl ==>> ", xsUrl);

        self.xsUrl = xsUrl;
        // console.log(" self.xsUrl ==>> ", self.xsUrl);

        this.axios({
          methods: "GET",
          url: "http://api.pingcc.cn/?xsurl1=" + xsUrl
        }).then(results => {
          // console.log("results ==>> ", results);

          let URL = this.$route.query.xsUrl;
          // console.log("URL ==>> ", URL);

          self.xsurl = results.data.list;
          // console.log(" self.xsurl ==>> ", self.xsurl);

          // console.log("this.i ==>> ", this.i);

          self.xsurlN = self.xsurl[this.i].num;
          // console.log("xsurlN ==>> ", self.xsurlN);

          self.conUrl = self.xsurl[this.i].url;
          // console.log("conUrl ==>> ", self.conUrl);

          let matterData = localStorage.getItem("collectData");
          matterData = matterData ? JSON.parse(matterData) : [];
          // console.log("matterData ==>> ", matterData);

          for (let i = 0; i < matterData.length; i++) {
            if (matterData[i].xsUrl == URL) {
              matterData[i].i = this.i;
              matterData[i].conUrl = self.conUrl;
              matterData[i].xsName = self.xsurlN;
            }
          }
          localStorage.setItem("collectData", JSON.stringify(matterData));
        });
      }

      if (this.$route.query.index != undefined) {
        // 章节列表
        let xsurl = this.$route.query.xsurl;
        // console.log("xsurl ==>> ", xsurl);

        self.xsurlN = xsurl.list[this.i].num;
        // console.log(" self.xsurlN ==>> ", self.xsurlN);

        self.conUrl = xsurl.list[this.i].url;
        // console.log("conUrl ==>> ", self.conUrl);
      }
      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?xsurl2=" + self.conUrl
      }).then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("matterModule/getmatterData", result);
        this.$toast.clear();
        this.$refs.matterBox.scrollTop = 0;
      });
    },

    goto() {
      if (this.i > 0) {
        this.$toast.loading({
          duration: 0,
          message: "加载中..."
        });

        if (this.$route.query.i != undefined) {
          this.i--;
          // console.log("this.i ==>> ", this.i);
        }

        if (this.$route.query.index != undefined) {
          this.i--;
        }

        this.gotoSection();
      } else {
        this.i = 0;
        this.$toast.clear();
      }
    },

    getto() {
      // console.log("this.xsurlL", this.xsurlL);
      if (this.i < this.xsurlL) {
        this.$toast.loading({
          duration: 0,
          message: "加载中..."
        });

        if (this.$route.query.i != undefined) {
          this.i++;
          // console.log("this.i ==>> ", this.i);
        }

        if (this.$route.query.index != undefined) {
          this.i++;
        }

        this.gotoSection();
      } else {
        this.i = this.xsurlL - 1;
        this.$toast.clear();
      }
    }
  },
  created() {
    // //开启加载提示
    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });

    // 重置state
    this.resetState();

    let self = this;

    this.i = this.$route.query.index;
    // console.log("this.i ==>> ", this.i);

    // 名字id
    let xsUrl = this.$route.query.xsUrl;
    // console.log("xsUrl ==>> ", xsUrl);

    this.xsUrl = xsUrl
    // console.log("this.xsUrl ==>> ",  this.xsUrl )
    
    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?xsurl1=" + xsUrl
    }).then(results => {
      // console.log("results ==>> ", results);
      // console.log("results.list ==>> ", results.data.list);
      self.xsurlL = results.data.list.length;
      // console.log("xsurlL ==>> ", self.xsurlL);
    });

    if (this.$route.query.xsurl == undefined) {
      self.conUrl = this.$route.query.url;
      // console.log("conUrl ==>> ", self.conUrl);

      self.xsurlN = this.$route.query.xsName;
      // console.log("xsurlN ==>> ", self.xsurlN);

      self.i = this.$route.query.i;
      // console.log(" self.i ==>> ", self.i);

      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?xsurl2=" + self.conUrl
      }).then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("matterModule/getmatterData", result);
        this.$toast.clear();
      });
      return;
    }

    // 章节
    let xsurl = this.$route.query.xsurl;
    // console.log(" xsurl ==>> ", xsurl);

    // 章节列表
    let xsurlLs = xsurl.list;

    self.xsurlL = xsurl.list;
    // console.log(" self.xsurlL ==>> ", self.xsurlL);

    self.conUrl = xsurlLs[this.i].url;
    // console.log("conUrl ==>> ", self.conUrl);

    // 第...章节名
    self.xsurlN = xsurl.list[this.i].num;
    // console.log(" self.xsurlN ==>> ", self.xsurlN);

    let matterData = localStorage.getItem("collectData");
    matterData = matterData ? JSON.parse(matterData) : [];
    // console.log("matterData ==>> ", matterData);

    for (let i = 0; i < matterData.length; i++) {
      if (matterData[i].url == URL) {
        matterData[i].conUrl = self.conUrl;
        matterData[i].xsName = self.xsurlL[this.i].num;
        matterData[i].i = this.i;
      }
    }

    localStorage.setItem("collectData", JSON.stringify(matterData));

    self.xsurlL = xsurlLs.length;
    // console.log("mhurlL ==>> ", self.mhurlL);

    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?xsurl2=" + xsurlLs[self.i].url
    }).then(result => {
      // console.log("result ==>> ", result);
      this.$store.commit("matterModule/getmatterData", result);
      this.$toast.clear();
    });
  }
};
</script>
<style>
.matter .van-nav-bar .van-icon {
  color: #e4393c;
}
.matter .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.matter .van-nav-bar__title {
  color: #e4393c;
  font-size: 20px;
}
</style>
<style lang="less" scoped>
.matter {
  height: 100%;
}
.matter-box {
  //   height: 100%;
  background: rgb(230, 166, 84);
  position: fixed;
  top: 46px;
  bottom: 0px;
  right: 0;
  left: 0;
  overflow-y: auto;
  .name {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
  }
  .text {
    padding: 10px 8px 5px 8px;

    p {
      font-size: 18px;
    }
  }
  .btn {
    width: 100%;
    margin: 10px 0;
    font-size: 18px;
    text-align: center;
  }
  .fl {
    width: calc(~ " 100% / 3 ");
  }
}
</style>